import moment from "moment";
import IMAGES from "../../../../../assets/images";

function MedicalCertificatePDF(props) {
  const { MedicalCertificateData, personalDetails, signature } = props;

  return (
    <div>
      <div className="d-flex justify-content-center mb-4">
        <img
          src={IMAGES.LOGO}
          alt="FamilyDoc 24/7 Logo"
          style={{ width: "22%" }}
        />
      </div>
      <div className="text-center">
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>77 Camden Street Lower, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : contact@familydoc247.ie</h6>
      </div>
      <div className="py-4">
        <h1 align="center">Medical Certificate</h1>
      </div>
      <div className="d-flex">
        <p className="pe-2">
          <span className="font-weight-600">Re: </span>
          {personalDetails?.[0]?.patient}
        </p>
        <p className="pe-2">
          <span className="font-weight-600">Address: </span>
          {personalDetails?.[0]?.currentAddress
            ? personalDetails?.[0]?.currentAddress
            : "N/A"}
        </p>
        <p>
          <span className="font-weight-600">DOB: </span>
          {personalDetails?.[0]?.dob
            ? moment(personalDetails?.[0]?.dob).format("DD/MM/YYYY")
            : "N/A"}
        </p>
      </div>

      <p className="py-3">
        This is to certify in my opinion that the above named patient is/was
        suffering from {MedicalCertificateData?.sufferingFrom}
      </p>
      <div className="pt-2 pb-4 d-flex">
        <p>
          And is <span className="font-weight-600">unable</span> to attend{" "}
          {MedicalCertificateData?.unabletoAttendField}
        </p>
      </div>
      <div>
        <p>
          <span className="font-weight-600">From: </span>
          {moment(MedicalCertificateData?.fromDate).format("DD/MM/YYYY")
            ? moment(MedicalCertificateData?.fromDate).format("DD/MM/YYYY")
            : "N/A"}
        </p>
        <p>
          <span className="font-weight-600">To: </span>
          {moment(MedicalCertificateData?.toDate).format("DD/MM/YYYY")
            ? moment(MedicalCertificateData?.toDate).format("DD/MM/YYYY")
            : "N/A"}
        </p>
      </div>
      <div className="pt-4 d-flex">
        <div className="me-5">
          <p>
            MCRN{" "}
            {personalDetails?.[0]?.mcrn ? personalDetails?.[0]?.mcrn : "N/A"}
          </p>

          <p className="mb-0">
            Dr{" "}
            {personalDetails?.[0]?.doctor
              ? personalDetails?.[0]?.doctor
              : "N/A"}
          </p>
        </div>
        {signature?.doctorSignature ? (
          <div
            style={{
              display: "inline-block",
              width: "100px",
              height: "50px",
            }}
          >
            <img
              src={signature?.doctorSignature}
              alt="signature"
              width="140px"
              height="100px"
            />
          </div>
        ) : (
          <p className="text-decoration-underline">No Signature Found</p>
        )}
      </div>
    </div>
  );
}

export default MedicalCertificatePDF;
