import React from "react";
import { Col, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import moment from "moment";

import ProfileEdit from "./ProfileEdit";

export default function PersonalInformation({
  profileQuery,
  imageUrl,
  setProfileImage,
  setIsEditing,
  loading,
}) {
  const { getPatientData, isLoading, isError, isSuccess } = useSelector(
    (state) => state?.patient
  );

  return (
    <>
      {profileQuery === "info" &&
        (isLoading ? (
          <p
            className="my-5 text-center fst-italic"
            style={{
              color: "#999999",
            }}
          >
            Loading...
          </p>
        ) : isSuccess ? (
          <Row className="gy-4 gx-2 mt-4">
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Name</p>
              <h5 className="profile__field-value">
                {getPatientData?.firstName
                  ? `${getPatientData?.firstName} ${getPatientData?.lastName}`
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">DOB</p>
              <h5 className="profile__field-value">
                {getPatientData?.dob
                  ? moment(getPatientData?.dob).format("DD/MM/YYYY")
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Gender</p>
              <h5 className="profile__field-value">
                {getPatientData?.gender ? getPatientData?.gender : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Patient Local GP</p>
              <h5 className="profile__field-value">
                {getPatientData?.patientLocalGP
                  ? getPatientData?.patientLocalGP
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Email</p>
              <h5 className="profile__field-value">
                {getPatientData?.email ? getPatientData?.email : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Phone Number</p>
              <h5 className="profile__field-value">
                {getPatientData?.phoneNumber
                  ? getPatientData?.phoneNumber
                  : "N/A"}
              </h5>
            </Col>
            {/* <Col xl={4} sm={6}>
              <p className="profile__field-label">Next of Kin Name</p>
              <h5 className="profile__field-value">
                {getPatientData?.nextOfKinName
                  ? getPatientData?.nextOfKinName
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Next of Kin Number</p>
              <h5 className="profile__field-value">
                {getPatientData?.nextOfKinNumber
                  ? getPatientData?.nextOfKinNumber
                  : "N/A"}
              </h5>
            </Col> */}
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Current Location</p>
              <h5 className="profile__field-value">
                {getPatientData?.currentAddress
                  ? getPatientData?.currentAddress
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Pharmacy Name</p>
              <h5 className="profile__field-value">
                {getPatientData?.pharmacyName
                  ? getPatientData?.pharmacyName
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Pharmacy Location</p>
              <h5 className="profile__field-value">
                {getPatientData?.pharmacyLocation
                  ? getPatientData?.pharmacyLocation
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">County</p>
              <h5 className="profile__field-value">
                {getPatientData?.country ? getPatientData?.country : "N/A"}
              </h5>
            </Col>
            {/* <Col xl={4} sm={6}>
              <p className="profile__field-label">Pharmacy Email</p>
              <h5 className="profile__field-value">
                {getPatientData?.pharmacyEmail
                  ? getPatientData?.pharmacyEmail
                  : "N/A"}
              </h5>
            </Col> */}
          </Row>
        ) : isError ? (
          <p className="my-5 text-center text-danger fst-italic">
            Network Error
          </p>
        ) : null)}
      {profileQuery === "edit" && (
        <ProfileEdit
          profileQuery={profileQuery}
          imageUrl={imageUrl}
          setProfileImage={setProfileImage}
          setIsEditing={setIsEditing}
          loading={loading}
        />
      )}
    </>
  );
}
