import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { FieldError } from "../../../../../components/ErrorMessage/ErrorMessage";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { updatedProfile } from "../../../../../app/features/auth/auth.slice";

import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { PATH } from "../../../../../utils/path";
import GenericDatePicker from "../../../../../components/GenericDatePicker";
import { ReactSearchAutocomplete } from "react-search-autocomplete";
import {
  getPatient,
  updatePatient,
} from "../../../../../app/features/patient/patient.slice";
import moment from "moment";
import {
  createPatientPharmacy,
  getAllPatientPharmacy,
  getPatientPharmacyData,
} from "../../../../../app/features/patientPharmacy/patientPharmacy.slice";

export default function ProfileEdit({
  profileQuery,
  imageUrl,
  setProfileImage,
  setIsEditing,
  loading,
}) {
  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm();
  const user = JSON.parse(localStorage.getItem("family_doc_app")) || {};
  const { getPatientData, isLoading, isError, isSuccess } = useSelector(
    (state) => state?.patient
  );
  const [phoneValue, setPhoneValue] = useState(getPatientData?.phoneNumber);
  const [showPharmacyId, setShowPharmacyId] = useState();
  const [selectDate, setSelectDate] = useState(
    getPatientData?.dob ? new Date(getPatientData?.dob.split("T")[0]) : null
  );
  const [selectedPharmacy, setSelectedPharmacy] = useState({});
  const [searchPharmacyName, setSearchPharmacyName] = useState();

  const { patientPharmacy } = useSelector((state) => state || {});
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const onClear = () => {
    setValue("pharmacyLocation", "");
    setValue("country", "");
    setSelectedPharmacy({});
  };
  const handleOnSearch = (string, results) => {
    if (results) {
      setValue("pharmacyLocation", results?.location);
      setValue("country", results?.country);
      setSelectedPharmacy(results);
      setShowPharmacyId(results?.id);
    } else {
      setValue("pharmacyLocation", "");
      setValue("country", "");
      setSelectedPharmacy({});
    }
  };

  const handleOnSelect = (item) => {
    if (item) {
      setSearchPharmacyName(item?.name);
      setValue("pharmacyLocation", item?.location);
      setValue("country", item?.country);
      setSelectedPharmacy(item);
      setShowPharmacyId(item?.id);
    }
  };

  const formatResult = (item) => {
    return (
      <>
        <span
          style={{ display: "block", textAlign: "left" }}
          className="fw-bold text-wrap"
        >
          {item?.name}
        </span>
        <span
          style={{ display: "block", textAlign: "left", fontSize: "15px" }}
          className="text-wrap"
        >
          {item?.location}
        </span>
      </>
    );
  };

  /** Personal Information Patient */

  useEffect(() => {
    const finalData = {
      PatientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, user?.userId]);

  useEffect(() => {
    const finalData = {
      pharmacyName: null,
      pharmacyLocation: null,
    };
    dispatch(getAllPatientPharmacy(finalData));
  }, [dispatch]);

  const getPersonalInfo = () => {
    setIsEditing(true);
    const finalData = {
      patientId: user.userId,
    };
    dispatch(getPatient(finalData));
    navigate(`${PATH.PATIENT_PROFILE}?profile=info`);
  };

  function moveToNext({ pharmacyData }, response) {
    const finalData = {
      pharmacyId: showPharmacyId ? showPharmacyId : 0,
      pharmacyName: searchPharmacyName || getPatientData?.pharmacyName,
      pharmacyLocation: pharmacyData?.pharmacyLocation,
      country: pharmacyData?.country,
      ig_xcord: "",
      ig_ycord: "",
      userId: user?.userId,
      pharmacyEmail: "",
    };
    dispatch(createPatientPharmacy({ finalData, getPersonalInfo }));
    setProfileImage({
      imageUrl: imageUrl,
      userName: `${response?.firstName} ${response?.lastName}`,
    });
    const selectedPharmacyData = patientPharmacy?.allPatientPharmacyData?.find(
      (item) => item.pharmacyName === searchPharmacyName
    );
    if (selectedPharmacyData) {
      setValue("pharmacyLocation", selectedPharmacyData?.pharmacyLocation);
      setValue("country", selectedPharmacyData?.country);
      setSelectedPharmacy(selectedPharmacyData);
    }
  }
  
  function onSubmit(data) {
    const { pharmacyLocation, country, ...restData } = data;
    const finalData = {
      patientId: user?.userId,
      firstName: restData.firstName,
      lastName: restData.lastName,
      image: imageUrl?.keyName
        ? imageUrl?.keyName
        : getPatientData?.image
        ? getPatientData?.image
        : "",
      dob: selectDate ? moment(selectDate).format("YYYY-MM-DD") : null,
      genderId: +restData.gender,
      patientLocalGP: restData.patientLocalGP,
      phoneNumber: phoneValue
        ? phoneValue
        : getPatientData?.phoneNumber
        ? getPatientData?.phoneNumber
        : "",
      countryCode: "",
      remainingPhone: "",
      currentAddress: restData.currentAddress,
      nextOfKinName: restData.nextOfKinName,
      nextOfKinNumber: restData.nextOfKinNumber,
    };

    const pharmacyData = {
      pharmacyLocation,
      country,
    };

    const callBackFunc = () => {
      dispatch(updatedProfile());
    };
    dispatch(
      updatePatient({ finalData, moveToNext, pharmacyData, callBackFunc })
    );
  }

  return (
    <div className="profile__edit-section">
      {isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        <>
          {profileQuery === "edit" && (
            <Form onSubmit={handleSubmit(onSubmit)}>
              <Row className="gy-4 gx-5 mt-4">
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    First Name
                  </Form.Label>
                  <Form.Control
                    // name="firstName"
                    type="text"
                    placeholder="John"
                    {...register("firstName")}
                    defaultValue={getPatientData?.firstName}
                  />
                  {errors.firstName && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Last Name
                  </Form.Label>
                  <Form.Control
                    // name="lastName"
                    type="text"
                    placeholder="Smith"
                    {...register("lastName")}
                    defaultValue={getPatientData?.lastName}
                  />
                  {errors.lastName && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    D.O.B
                  </Form.Label>
                  <GenericDatePicker
                    selectedDateState={selectDate}
                    onChange={(date) => setSelectDate(date)}
                    useDrDateClass={false}
                    dateFormat="dd/MM/yyyy"
                    placeholderText="day/month/year"
                  />
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Gender
                  </Form.Label>
                  <Form.Select
                    // name="gender"
                    className="Select-Dropdown-Sizing"
                    {...register("gender")}
                    defaultValue={getPatientData?.gender}
                  >
                    <option value={101}>Male</option>
                    <option value={102}>Female</option>
                    <option value={103}>Other</option>
                  </Form.Select>
                  {errors.gender && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Patient Local GP
                  </Form.Label>
                  <Form.Control
                    // name="patientLocalGP"
                    type="text"
                    placeholder="Patient Local GP"
                    {...register("patientLocalGP")}
                    defaultValue={getPatientData?.patientLocalGP}
                  />
                  {errors.patientLocalGP && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Email
                  </Form.Label>
                  <Form.Control
                    // name="email"
                    type="email"
                    disabled
                    placeholder="patient@gmail.com"
                    {...register("email", {
                      pattern: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/,
                    })}
                    defaultValue={getPatientData?.email}
                  />
                  {errors.email && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                    Phone Number
                  </Form.Label>
                  <PhoneInput
                    country="ie"
                    className="Phone-Number-FieldWidth border rounded"
                    placeholder="8945 898 898"
                    value={getPatientData?.phoneNumber}
                    onChange={(e) => setPhoneValue(`+${e}`)}
                  />
                  {errors.phoneNumber && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                {/* <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Next of Kin Name
                  </Form.Label>
                  <Form.Control
                    name="nextOfKinName"
                    type="text"
                    placeholder="Next of Kin Name"
                    {...register("nextOfKinName")}
                    defaultValue={getPatientData?.nextOfKinName}
                  />
                  {errors.nextOfKinName && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Next of Kin Number
                  </Form.Label>
                  <Form.Control
                    name="nextOfKinNumber"
                    type="text"
                    placeholder="Next of Kin Number"
                    {...register("nextOfKinNumber")}
                    defaultValue={getPatientData?.nextOfKinNumber}
                  />
                  {errors.nextOfKinNumber && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col> */}
                <Col xl={4} sm={6}>
                  <div className="d-flex justify-content-between align-items-baseline">
                    <span className="w-100">
                      <p className="mb-2">Pharmacy Name</p>
                      {patientPharmacy?.allPatientPharmacyData && (
                        <ReactSearchAutocomplete
                          items={patientPharmacy?.allPatientPharmacyData?.map(
                            (option) => {
                              return {
                                id: option?.pharmacyId,
                                name: option?.pharmacyName,
                                location: option?.pharmacyLocation,
                                country: option?.country,
                                display: `${option?.pharmacyName} - ${option?.pharmacyLocation}`,
                              };
                            }
                          )}
                          placeholder="Select Pharmacy Name"
                          onClear={onClear}
                          onSearch={handleOnSearch}
                          onSelect={handleOnSelect}
                          formatResult={formatResult}
                          // autoFocus
                          inputSearchString={getPatientData?.pharmacyName ?? ""}
                          styling={{
                            height: "46px",
                            border: "1px solid #dee2e6",
                            borderRadius: "0.375rem",
                            boxShadow: "none",
                          }}
                          className="auto__search-field"
                        />
                      )}
                    </span>
                  </div>
                </Col>
                {/* <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Pharmacy Name
                  </Form.Label>
                  <Form.Group>
                    <Form.Control
                      list="pharmacyNameList"
                      className="Input-Height"
                      defaultValue={getPatientData?.pharmacyName ?? ""}
                      {...register("pharmacyName")}
                      onChange={(e) => {
                        const selectedPharmacyData =
                          patientPharmacy?.allPatientPharmacyData?.find(
                            (item) => item.pharmacyName === e.target.value
                          );
                        if (selectedPharmacyData) {
                          setValue(
                            "pharmacyLocation",
                            selectedPharmacyData.pharmacyLocation
                          );
                          setValue("country", selectedPharmacyData.country);
                          setSelectedPharmacy(selectedPharmacyData);
                          setShowPharmacyId(selectedPharmacyData.pharmacyId);
                        } else {
                          setValue("pharmacyLocation", "");
                          setValue("country", "");
                          setSelectedPharmacy({});
                        }
                      }}
                      placeholder="Select Pharmacy Name"
                    />
                  </Form.Group>
                </Col> */}

                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                    Pharmacy Address
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Select Address"
                    {...register("pharmacyLocation")}
                    defaultValue={
                      getPatientData?.pharmacyLocation
                        ? getPatientData?.pharmacyLocation
                        : ""
                    }
                  />
                  {errors.currentAddress && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                    County
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="County"
                    {...register("country")}
                    defaultValue={
                      getPatientData?.country ? getPatientData?.country : ""
                    }
                  />
                </Col>
                {/* <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                   Pharmacy Email
                  </Form.Label>
                  <Form.Control
                    name="pharmacyEmail"
                    type="text"
                    placeholder="Pharmacy Email"
                    {...register(
                      "pharmacyEmail"
                    )}
                    defaultValue={
                      getPatientData?.pharmacyEmail
                        ? getPatientData?.pharmacyEmail
                        : ""
                    }
                  />
                  {errors.pharmacyEmail && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col> */}
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                    Current Location
                  </Form.Label>
                  <Form.Control
                    // name="currentAddress"
                    type="text"
                    placeholder="4517 Washington Ave. Manchester, Kentucky 39495"
                    {...register("currentAddress")}
                    defaultValue={
                      getPatientData?.currentAddress
                        ? getPatientData?.currentAddress
                        : ""
                    }
                  />
                  {errors.currentAddress && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
              </Row>
              <div className="mt-4 d-flex flex-wrap justify-content-end">
                <Button
                  onClick={() =>
                    navigate(`${PATH.PATIENT_PROFILE}?profile=info`)
                  }
                  className="Save-Changes-Btn border-0 text-black"
                  style={{
                    background: "#eae5e5",
                    borderColor: "#eae5e5",
                  }}
                >
                  Cancel
                </Button>
                <Button
                  className="Save-Changes-Btn"
                  style={{ background: "#000071" }}
                  type="submit"
                  disabled={loading}
                >
                  Save Changes
                </Button>
              </div>
            </Form>
          )}
        </>
      ) : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null}
    </div>
  );
}
