import { useState, useEffect } from "react";
import { Accordion } from "react-bootstrap";
import { BsPencilSquare } from "react-icons/bs";
import { RiDeleteBin5Fill } from "react-icons/ri";

import { useDispatch, useSelector } from "react-redux";
import PastMedicalHistory from "../../../../components/PastMedicalHistory";
import CurrentMedication from "../../../../components/CurrentMedication";
import AllergyHistory from "../../../../components/AllergyHistory";
import { ConfirmationModal } from "../../../../components/Modal/ConfirmationModal";
import {
  deletePatientHistory,
  getPatientHistory,
} from "../../../../app/features/patientHistory/patientHistory.slice";

function MedicalHistory({ getPatientData, user }) {
  const dispatch = useDispatch();

  const [deleteModel, setDeleteModel] = useState(false);
  const [deleteId, setDeleteId] = useState("");
  const [editing, setEditing] = useState({
    pastMedical: false,
    currentMedication: false,
    allergy: false,
  });

  const { patientHistoryData, isLoading, isSuccess, isError } = useSelector(
    (state) => state.patientHistory
  );

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  }, []);

  // useEffect(() => {
  //   const finalData = {
  //     PatientId: user?.userId,
  //   };
  //   dispatch(getPatientPharmacyData(finalData));
  // }, [dispatch, user?.userId]);

  /** Delete Medical */
  const moveToNextDeleteMedicalHistory = () => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  };
  const onDelete = () => {
    const finalData = {
      patientId: user?.userId,
      historyTypeId: deleteId ? deleteId : null,
    };
    dispatch(
      deletePatientHistory({ finalData, moveToNextDeleteMedicalHistory })
    );
  };

  return (
    <>
      {isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        <>
          <Accordion className="px-4 mt-5" defaultActiveKey="0">
            <Accordion.Item eventKey="0" className="my-3 ">
              <Accordion.Header>
                <h5>Past Medical History</h5>
                <span style={{ color: "#FF3A3A" }} className="fw-bold mx-1">
                  *
                </span>
              </Accordion.Header>
              <Accordion.Body className="py-4">
                {patientHistoryData?.pastMedicalHistory &&
                !editing.pastMedical ? (
                  <div className="d-flex justify-content-between py-3">
                    <div className="pe-2">
                      <p
                        style={{
                          textAlign: "justify",
                          marginRight: "10px",
                        }}
                      >
                        {patientHistoryData?.pastMedicalHistory}
                      </p>
                    </div>
                    <div>
                      <div
                        className="d-flex justify-content-center align-items-center mb-2"
                        style={{
                          height: "29px",
                          width: "32px",
                          backgroundColor: "#E1EBFF",
                          borderRadius: "5px",
                          cursor: "pointer",
                        }}
                        onClick={() =>
                          setEditing((prev) => ({
                            ...prev,
                            pastMedical: true,
                          }))
                        }
                      >
                        <BsPencilSquare
                          style={{
                            color: "#2269F2",
                            fontSize: "18px",
                          }}
                        />
                      </div>
                      <div
                        className="d-flex justify-content-center align-items-center"
                        style={{
                          height: "29px",
                          width: "32px",
                          backgroundColor: "#FFDADD",
                          borderRadius: "5px",
                          cursor: "pointer",
                        }}
                        onClick={() => {
                          setDeleteModel(true);
                          setDeleteId(1);
                        }}
                      >
                        <RiDeleteBin5Fill
                          style={{
                            color: "#E63946",
                            fontSize: "18px",
                          }}
                        />
                      </div>
                    </div>
                  </div>
                ) : (
                  <PastMedicalHistory
                    setEditing={(status) =>
                      setEditing((prev) => ({
                        ...prev,
                        pastMedical: status,
                      }))
                    }
                  />
                )}
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>

          <Accordion className="px-4" defaultActiveKey="1">
            <Accordion.Item eventKey="0" className="my-3">
              <Accordion.Header>
                <h5>Current Medications</h5>
                <span style={{ color: "#FF3A3A" }} className="fw-bold mx-1">
                  *
                </span>
              </Accordion.Header>
              <Accordion.Body className="py-4">
                {patientHistoryData?.currentMedical &&
                !editing.currentMedication ? (
                  <div className="d-flex justify-content-between py-3">
                    <div className="pe-2">
                      <p
                        style={{
                          textAlign: "justify",
                          marginRight: "10px",
                        }}
                      >
                        {patientHistoryData?.currentMedical}
                      </p>
                    </div>
                    <div>
                      <div
                        className="d-flex justify-content-center align-items-center mb-2"
                        style={{
                          height: "29px",
                          width: "32px",
                          backgroundColor: "#E1EBFF",
                          borderRadius: "5px",
                          cursor: "pointer",
                        }}
                        onClick={() =>
                          setEditing((prev) => ({
                            ...prev,
                            currentMedication: true,
                          }))
                        }
                      >
                        <BsPencilSquare
                          style={{
                            color: "#2269F2",
                            fontSize: "18px",
                          }}
                        />
                      </div>
                      <div
                        className="d-flex justify-content-center align-items-center"
                        style={{
                          height: "29px",
                          width: "32px",
                          backgroundColor: "#FFDADD",
                          borderRadius: "5px",
                          cursor: "pointer",
                        }}
                        onClick={() => {
                          setDeleteModel(true);
                          setDeleteId(2);
                        }}
                      >
                        <RiDeleteBin5Fill
                          style={{
                            color: "#E63946",
                            fontSize: "18px",
                          }}
                        />
                      </div>
                    </div>
                  </div>
                ) : (
                  <CurrentMedication
                    setEditing={(status) =>
                      setEditing((prev) => ({
                        ...prev,
                        currentMedication: status,
                      }))
                    }
                  />
                )}
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>

          <Accordion className="px-4" defaultActiveKey="2">
            <Accordion.Item eventKey="0" className="my-3">
              <Accordion.Header>
                <h5>Allergy (to any medication)</h5>
                <span style={{ color: "#FF3A3A" }} className="fw-bold mx-1">
                  *
                </span>
              </Accordion.Header>
              <Accordion.Body className="py-4">
                {patientHistoryData?.allergyHistory && !editing.allergy ? (
                  <div className="d-flex justify-content-between py-3">
                    <div className="pe-2">
                      <p
                        style={{
                          textAlign: "justify",
                          marginRight: "10px",
                        }}
                      >
                        {patientHistoryData?.allergyHistory}
                      </p>
                    </div>
                    <div>
                      <div
                        className="d-flex justify-content-center align-items-center mb-2"
                        style={{
                          height: "29px",
                          width: "32px",
                          backgroundColor: "#E1EBFF",
                          borderRadius: "5px",
                          cursor: "pointer",
                        }}
                        onClick={() =>
                          setEditing((prev) => ({
                            ...prev,
                            allergy: true,
                          }))
                        }
                      >
                        <BsPencilSquare
                          style={{
                            color: "#2269F2",
                            fontSize: "18px",
                          }}
                        />
                      </div>
                      <div
                        className="d-flex justify-content-center align-items-center"
                        style={{
                          height: "29px",
                          width: "32px",
                          backgroundColor: "#FFDADD",
                          borderRadius: "5px",
                          cursor: "pointer",
                        }}
                        onClick={() => {
                          setDeleteModel(true);
                          setDeleteId(3);
                        }}
                      >
                        <RiDeleteBin5Fill
                          style={{
                            color: "#E63946",
                            fontSize: "18px",
                          }}
                        />
                      </div>
                    </div>
                  </div>
                ) : (
                  <AllergyHistory
                    setEditing={(status) =>
                      setEditing((prev) => ({ ...prev, allergy: status }))
                    }
                  />
                )}
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
        </>
      ) : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null}

      {deleteId && (
        <ConfirmationModal
          show={deleteModel}
          onHide={() => setDeleteModel(false)}
          heading={`Delete ${
            deleteId === 1
              ? "Past Medical"
              : deleteId === 2
              ? "Current Medical"
              : "Allergy"
          } History`}
          title={`this ${
            deleteId === 1
              ? "Past Medical"
              : deleteId === 2
              ? "Current Medical"
              : "Allergy"
          } History`}
          removeFunc={onDelete}
        />
      )}
    </>
  );
}

export default MedicalHistory;
