import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import PersonalProfileLayout from "../../../components/Layout/PersonalProfileLayout";
import PersonalInformation from "./PersonalInformation";
import useQueryParam from "../../../hook/useQueryParam";
import { getPatient } from "../../../app/features/patient/patient.slice";
import MedicalHistory from "./MedicalHistory";
import Notes from "./Notes";
import ReferralNotes from "./ReferralNotes";
import OnlinePrescForm from "./OnlinePrescForm";
import DoctorProfileChangePassword from "./DoctorsProfileComponent/DoctorProfileChangePassword";

const navEventLinks = [
  {
    key: "personalInfo",
    name: "Personal Information",
  },
  {
    key: "medicalHistory",
    name: "Medical History",
  },
  {
    key: "notes",
    name: "Notes",
  },
  {
    key: "referralNotes",
    name: "Referral Notes",
  },
  {
    key: "onlinePrescriptionHistory",
    name: "Online Prescription History",
  },
  {
    key: "changePass",
    name: "Change Password",
  },
  
  {key: "deleteAcc",
  name: "Delete Account",
},
];

export default function PatientProfile({ setProfileImage }) {
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("family_doc_app")) || {};
  const { getPatientData, isLoading, isSuccess, isError } = useSelector(
    (state) => state.patient
  );
  const [isEditing, setIsEditing] = useState(true);
  const [imageUrl, setImageUrl] = useState();
  const [loading, setLoading] = useState(false);
  const [notesTrigger, setNotesTrigger] = useState(false);
  const [referralNotesTrigger, setReferralNotesTrigger] = useState(false);

  const query = useQueryParam();
  const profileQuery = query.get("profile");

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };

    dispatch(getPatient(finalData));
  }, [dispatch, user?.userId]);

  const handleNavClick = (key) => {
    if (key === "notes") {
      setNotesTrigger(true);
      setReferralNotesTrigger(false);
    } else if (key === "referralNotes") {
      setReferralNotesTrigger(true);
      setNotesTrigger(false);
    } else {
      setNotesTrigger(false);
      setReferralNotesTrigger(false);
    }
  };
  return (
    <>
      <PersonalProfileLayout
        navEventLinks={navEventLinks}
        setImageUrl={setImageUrl}
        imageUrl={imageUrl}
        setIsEditing={setIsEditing}
        loading={loading}
        setLoading={setLoading}
        getUserData={getPatientData}
        onNavClick={handleNavClick}
        deleteAccount={true}
      >
        <div eventKey="personalInfo" className="tab__content-body">
          <p className="main-title m-0">Personal Details</p>
          <PersonalInformation
            profileQuery={profileQuery}
            imageUrl={imageUrl}
            setProfileImage={setProfileImage}
            setIsEditing={setIsEditing}
            loading={loading}
          />
        </div>
        <div eventKey="medicalHistory" className="tab__content-body">
          <p className="main-title m-0">Medical History</p>

          <MedicalHistory user={user} getPatientData={getPatientData} />
        </div>
        <div eventKey="notes" className="tab__content-body">
          <p className="main-title m-0">Notes</p>
          <Notes user={user} trigger={notesTrigger} />
        </div>
        <div eventKey="referralNotes" className="tab__content-body">
          <p className="main-title m-0">Referral Notes</p>
          <ReferralNotes user={user} trigger={referralNotesTrigger} />
        </div>
        <div eventKey="onlinePrescriptionHistory" className="tab__content-body">
          <p className="main-title m-0">Online Prescription History</p>
          <OnlinePrescForm user={user} trigger={referralNotesTrigger} />
        </div>
        <div eventKey="changePass" className="tab__content-body">
          <p className="main-title m-0">Change Password</p>
          <DoctorProfileChangePassword
            isError={isError}
            isLoading={isLoading}
            isSuccess={isSuccess}
          />
        </div>
      </PersonalProfileLayout>
    </>
  );
}
