import { useEffect, useMemo, useState } from "react";
import { Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { AiFillEye } from "react-icons/ai";
import moment from "moment";
import { getAllPatientPresForm } from "../../../../app/features/PatientOnlinePrescription/PatientOnlinePrescription.slice";
import DataTable from "../../../../components/Table/DataTable";
import OnlinePresFormModal from "./Modal";

function OnlinePrescForm(props) {
  const [showForm, setShowForm] = useState(false);
  const [formData, setFormData] = useState();
  const [pageNumber, setPageNumber] = useState(1);
  const pageSize = useMemo(() => 5, []);

  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const { getAllPatientOnlinePrescForm, isError, isLoading, isSuccess } =
    useSelector((state) => state?.patientOnlinePresc || []);

  const dispatch = useDispatch();

  useEffect(() => {
    const finalData = {
      PageNo: pageNumber,
      Size: pageSize,
      isPagination: true,
      FormStatusId: null,
      patientId: user?.userId,
    };
    dispatch(getAllPatientPresForm(finalData));
  }, [dispatch, pageNumber, user?.userId]);

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };

  const handleShowForm = (cell) => {
    setFormData(cell);
    setShowForm(true);
  };

  const columns = [
    {
      dataField: "formId",
      text: "ID",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "doctor",
      text: "Doctor Name",
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
      formatter: (cell) => {
        return cell || "N/A";
      },
    },
    {
      dataField: "orderDate",
      text: "Order Date",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        return cell ? moment(cell).format("DD/MM/YYYY") : "N/A";
      },
    },
    {
      dataField: "formStatus",
      text: "Status",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "isPrescriptionAdded",
      text: "Prescription",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, cell) => (
        <p className="m-0">
          {cell?.isPrescriptionAdded ? "Added" : "Not Added"}
        </p>
      ),
    },
    {
      dataField: "paymentStatus",
      text: "Payment Status",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1", width: "14rem" },
      formatter: (row, cell) => (
        <p
          className={`mb-0 text-center py-1 w-75 ${
            cell?.status === "InActive" ? "inactive-status" : "active-status"
          }`}
        >
          {cell?.status === "InActive" ? "Unpaid" : "Paid"}
        </p>
      ),
    },
    {
      dataField: "action",
      text: "Actions",
      sort: false,

      formatter: (row, cell) => {
        return (
          <>
            <Button
              onClick={() => handleShowForm(cell)}
              className="table-action bg-transparent border-0"
            >
              <AiFillEye /> Details
            </Button>
          </>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  return (
    <>
      <div className="mx-4 mt-4">
        {isLoading ? (
          <p
            className="my-5 text-center fst-italic"
            style={{
              color: "#999999",
            }}
          >
            Loading...
          </p>
        ) : isSuccess ? (
          <span className="doctor-datatable">
            <DataTable
              tableColumns={columns}
              tableData={
                getAllPatientOnlinePrescForm?.data
                  ? getAllPatientOnlinePrescForm?.data
                  : []
              }
              keyField="formId"
              pageNumber={pageNumber}
              totalRecords={getAllPatientOnlinePrescForm?.totalCounts}
              dataPerPage={pageSize}
              handlePageChange={handlePageChange}
            />
          </span>
        ) : isError ? (
          <p className="my-5 text-center text-danger fst-italic">
            Network Error
          </p>
        ) : null}
      </div>
      <OnlinePresFormModal
        show={showForm}
        handleClose={() => setShowForm(false)}
        formData={formData}
      />
    </>
  );
}

export default OnlinePrescForm;
