import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import { BsFillCalendarFill } from "react-icons/bs";
import { getAllPatientNotes } from "../../../../app/features/patientNotes/patientNotes.slice";

function Notes({ user, trigger }) {
  const { allPatientNotesData, isError, isLoading, isSuccess } = useSelector(
    (state) => state?.patientNotes
  );
  const dispatch = useDispatch();

  useEffect(() => {
    if (trigger) {
      const finalData = {
        noteTypeId: 1,
        patientId: user?.userId,
      };
      dispatch(getAllPatientNotes(finalData));
    }
  }, [trigger]);

  const DateAndDetails = ({ doctor, date, note }) => {
    return (
      <>
        <div className="d-flex justify-content-between align-items-center">
          <p className=" fs-5 fw-bold">{doctor}</p>
          <div className="d-flex">
            {date && (
              <>
                <BsFillCalendarFill size={20} />
                <p className="ps-2">{moment(date)?.format("DD/MM/YYYY")}</p>
              </>
            )}
          </div>
        </div>
        <p>Description: {note ? note : "N/A"}</p>
        <hr />
      </>
    );
  };
  const DateAndDetailsList = ({ data }) => {
    return (
      <div>
        {data?.map(({ doctor, date, note }) => (
          <DateAndDetails key={date} doctor={doctor} date={date} note={note} />
        ))}
      </div>
    );
  };

  return (
    <>
      {isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        <div className="px-4 mt-4 patient-notes-data">
          {allPatientNotesData?.length !== 0 ? (
            <DateAndDetailsList data={allPatientNotesData} />
          ) : (
            <p className="text-center">No Data</p>
          )}
        </div>
      ) : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null}
    </>
  );
}

export default Notes;
