import moment from "moment";
import IMAGES from "../../../../../assets/images";

function SpecialistLetterPDF({ stateData, personalDetails, signature }) {

  return (
    <div className="mb-5">
      <div className="d-flex justify-content-center mb-4">
        <img
          src={IMAGES.LOGO}
          alt="FamilyDoc 24/7 Logo"
          style={{ width: "22%" }}
        />
      </div>
      <div className="text-center">
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>77 Camden Street Lower, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : contact@familydoc247.ie</h6>
      </div>
      <p className="my-5">{stateData?.specialistLetter?.date
          ? moment(stateData?.specialistLetter?.date).format("DD/MM/YYYY")
          : "N/A"}</p>
      <p className="mb-5">
        <span className="font-weight-600">To: </span>
        {stateData?.specialistLetter?.toDoctor
          ? stateData?.specialistLetter?.toDoctor
          : "N/A"}
      </p>
      <div className="d-flex">
        <p className="pe-2">
          <span className="font-weight-600">Re: </span>
          {personalDetails?.[0]?.patient
            ? personalDetails?.[0]?.patient
            : "N/A"}
        </p>
        <p className="pe-2">
          <span className="font-weight-600">Address: </span>
          {personalDetails?.[0]?.currentAddress
            ? personalDetails?.[0]?.currentAddress
            : "N/A"}
        </p>
        <p>
          <span className="font-weight-600">DOB: </span>
          {moment(personalDetails?.[0]?.dob).format("DD/MM/YYYY")}
        </p>
      </div>
      <p>
        <span className="font-weight-600">Tel: </span>
        {personalDetails?.[0]?.phoneNumber
          ? personalDetails?.[0]?.phoneNumber
          : "N/A"}
      </p>
      <p className="mt-5">Dear Doctor,</p>
      <p>I would appreciate if you could see above-named patient.</p>
      <div className="mb-5">
        {stateData?.specialistLetter?.referralNote && (
          <div>
            <p className="text-black-50 mb-0">Referral Notes</p>
            {stateData?.specialistLetter?.referralNote
              ? stateData?.specialistLetter?.referralNote
              : "N/A"}
          </div>
        )}
        {stateData?.specialistLetter?.appointmentDetail && (
          <>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Presenting Complaint</p>
              {
                stateData?.specialistLetter?.appointmentDetail
                  ?.presentingComplaint
              }
            </div>
            {/* <div className="mb-3">
          <p className="text-black-50 mb-0">History of presenting complaint</p>
          {stateData?.appointmentDetail?.historyOfPresentingComplaint}
        </div> */}
            <div className="mb-3">
              <p className="text-black-50 mb-0">Past Medical History</p>
              {
                stateData?.specialistLetter?.appointmentDetail
                  ?.pastMedicalHistory
              }
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Medications</p>
              {stateData?.specialistLetter?.appointmentDetail?.medications}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Allergies</p>
              {stateData?.specialistLetter?.appointmentDetail?.allergies}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Family History</p>
              {stateData?.specialistLetter?.appointmentDetail?.familyHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Social History</p>
              {stateData?.specialistLetter?.appointmentDetail?.socialHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Objective Examination</p>
              {
                stateData?.specialistLetter?.appointmentDetail
                  ?.objectiveExamination
              }
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Suspected Diagnosis</p>
              {
                stateData?.specialistLetter?.appointmentDetail
                  ?.suspectedDiagnosis
              }
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Recommended Actions</p>
              {
                stateData?.specialistLetter?.appointmentDetail
                  ?.recommendedActions
              }
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Sign off</p>
              {stateData?.specialistLetter?.appointmentDetail?.signOff}
            </div>
          </>
        )}
      </div>
      <p className="py-3">
        The above-named patient prefers to see you privately.
      </p>
      <p className="pb-3">
        I would appreciate your expert review and further management.
      </p>
      <p className="pb-2">Yours sincerely,</p>
      <div className="d-flex">
        <div className="me-5">
          <p>
            MCRN{" "}
            {personalDetails?.[0]?.mcrn ? personalDetails?.[0]?.mcrn : "N/A"}
          </p>

          <p className="mb-0">
            Dr{" "}
            {personalDetails?.[0]?.doctor
              ? personalDetails?.[0]?.doctor
              : "N/A"}
          </p>
        </div>
        {signature?.doctorSignature ? (
          <div
            style={{
              display: "inline-block",
              width: "100px",
              height: "50px",
            }}
          >
            <img
              src={signature?.doctorSignature}
              alt="signature"
              width="140px"
              height="100px"
            />
          </div>
        ) : (
          <p className="text-decoration-underline">No Signature Found</p>
        )}
      </div>
    </div>
  );
}

export default SpecialistLetterPDF;
